<template>
  <div>
    <fTitle>
      <template #title>Icon 图标</template>
      <template #introduce>提供了一套常用的图标集合。</template>
    </fTitle>
    <sTitle>
      <template #title>图标集合</template>
      <template #introduce>直接通过设置类名为 el-icon-iconName 来使用即可。</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
<el-row v-for="item in iconList" :key="item.row">
  <el-col  v-for="child in item.children" :key="child.name" :span="4">
    <div @mouseenter="enter" @mouseleave="leave" class="grid-content">
    <i  :class=[child.name]></i>
    <span >{{ child.name }}</span></div>
  </el-col>
</el-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'IconIndex',
  data () {
    return {
      iconList: [
        {
          row: 1,
          children: [
            {
              name: 'el-icon-platform-eleme'
            },
            {
              name: 'el-icon-eleme'
            },
            {
              name: 'el-icon-delete-solid'
            },
            {
              name: 'el-icon-delete'
            },
            {
              name: 'el-icon-s-tools'
            },
            {
              name: 'el-icon-setting'
            }
          ]
        },
        {
          row: 2,
          children: [
            {
              name: 'el-icon-user-solid'
            },
            {
              name: 'el-icon-user'
            },
            {
              name: 'el-icon-phone'
            },
            {
              name: 'el-icon-phone-outline'
            },
            {
              name: 'el-icon-more'
            },
            {
              name: 'el-icon-more-outline'
            }
          ]
        },
