    enter (e) {
      e.target.style.color = '#409EFF'
    },
    leave (e) {
      e.target.style.color = ''
    }
  }
}
</script>

<style scoped lang="less">
@import '../../styles//row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;

    .grid-content{
     height: 150px;
     width: 100%;
     background: white;
     color: #909399;
     font-size: 50px;
     border-bottom: 1px solid darkgray;
     border-right: 1px solid darkgray;
     .active{
        color: #409EFF;
